html,
body {
  height: 100%;
  font-family: 'Poppins';
}

body {
  margin: 0;
  padding: 10px 0px;
}

#root {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.footer {
  color: white;
  float: left;
  display: flex;
  padding: 15px;
  background-color:#4E5DE1;
  background: linear-gradient(50deg,#4E5DE1 ,#252424 );
  background: radial-gradient(ellipse farthest-corner at 0 140%,#252424  0% #316dc2 70%, #252424  0% #4e95d3 70%);
 /* background-image: radial-gradient(
    ellipse farthest-corner at 0 300%,
    #4e5de1 70%,
    #252424 70%);*/
}

.footer p {
  padding: 30px 150px;
  line-height: 22px;
  font-size: 16px;
  text-align: center;
}

.footer span {
  font-size: 30px;
}
content p span{
  text-align:right;
  padding:10px 0px 0px 950px;
}

footer {
  background-image: linear-gradient(100deg, #4e5de1, #252424);
  color: white;
  font-size: 16px;
}

footer p{
  float:left;
  padding:5px 245px;
  font-size:16px;
}

.fade-in {
  animation: fadeIn ease 10s;
  -webkit-animation: fadeIn ease 10s;
  -moz-animation: fadeIn ease 10s;
  -o-animation: fadeIn ease 10s;
  -ms-animation: fadeIn ease 10s;
}
@keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-moz-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-webkit-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-o-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-ms-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

#pulse{
  display:flex;
  float:left;
  justify-content: space-evenly;
  padding:5px;
}

#pulse:hover{
  animation: pulse 0.5s ease-in infinite;
}
#push:hover{
  animation: pulse 0.3s linear 1;
}
#pop:hover{
  animation: pop 0.3s linear 1;
}
@keyframes pulse{
  25%  {transform: scale(0.9);}
  75%  {transform: scale(1.1);}
}
@keyframes push{
  50%  {transform: scale(0.8);}
}
@keyframes pop{
  50%  {transform: scale(1.2);}
}

.copy{
  text-align:right;
  padding:0px 0px 0px 50px;
}

@media only screen and (max-width:500px) {
  /* For mobile phones: */
.footer, footer{
   display:block;
   float:none;
   text-align: center;
   padding:10px 0px;
  }

  content {
    text-align:center;
    padding: 10px 10px;
    display:block;
    float:none;
  }

  .footer {
    text-align:center;
    float:none;
    display:block;
  }

  .footer p {
    padding: 10px 45px;
    font-size: 20px;
    text-align: left;
  }

  content p span{
    text-align:center;
    padding:10px 0px;
  }

  .footer span {
    font-size: 20px;
  }

  footer p{
    padding:10px 55px;
    font-size:16px;
    text-align:center;
  }
  

  .copy{
    text-align:center;
    padding:0px;
  }
}
@media (max-width: 768px) {   
  footer p{
  padding:10px 45px;
  font-size:16px;
  text-align:center;
} 

.footer p {
  padding: 10px 35px;
  font-size: 18px;
  text-align: left;
}}
@media (max-width: 992px) {   
  footer p{
  padding:10px 135px;
  font-size:16px;
  text-align:center;
} 

.footer p {
  padding: 10px 65px;
  font-size: 18px;
  text-align: left;
}}

@media (max-width: 567px) {   
  footer p{
  padding:10px 35px;
  font-size:16px;
  text-align:center;
} 
}